<template>
  <div>
    <index />
    <div class="content-page">
      <div class="main-content">
        <div class="addUser">
          <h4 class="font-size-18">Danh sách người dùng</h4>
          <router-link to="/list-user/add-user">
            <div
              class="btn-group float-right"
              :class="this.DATA_PERMISSION.includes('ADD_USER') ? '' : 'd-none'"
            >
              <button
                type="submit"
                class="btn btn-outline-primary"
                style="font-size: 13px;background-color: #EBF6FF;"
                data-toggle="modal"
                data-target=".Risk_QL-User_add"
                ><b-icon icon="plus-circle"></b-icon> Thêm mới</button
              >
            </div>
          </router-link>
        </div>

        <div class="searchInput">
          <form @submit.prevent="submitForm">
            <div class="box-fillter" style="padding: 2px 14px 2px 14px;">
              <div class="form-row">
                <div class="col-md-2 col-sm-4">
                  <div class="bf-detail">
                    <input
                      type="text"
                      style="font-size: 13px;"
                      class="form-control"
                      placeholder="Họ và tên"
                      v-model.trim="search.fullName"
                    />
                  </div>
                </div>

                <div class="col-md-2 col-sm-4">
                  <div class="bf-detail">
                    <input
                      type="text"
                      style="font-size: 13px;"
                      class="form-control"
                      placeholder="Email"
                      v-model.trim="search.email"
                    />
                  </div>
                </div>
                
                <div class="col-md-2 col-sm-4">
                  <div class="bf-detail">
                    <input
                      type="text"
                      style="font-size: 13px;"
                      class="form-control"
                      placeholder="Điện thoại"
                      v-model.trim="search.phoneNumber"
                    />
                  </div>
                </div>

                <div class="col-md-2 col-sm-4">
                  <div class="bf-detail">
                    <b-input-group class="mb-3">
                            <b-form-input
                            style="height: 34px;"
                              id="example-input"
                              class="form-control"
                              v-model="search.dateFrom"
                              type="text"
                              placeholder="Từ ngày"
                              autocomplete="off"
                            ></b-form-input>
                            <b-input-group-append>
                              <b-form-datepicker
                                v-model="search.dateFrom"
                                class="date"
                                button-only
                                right
                                locale="vi-VN"
                                style="height: 34px;"
                                aria-controls="example-input"
                              ></b-form-datepicker>
                            </b-input-group-append>
                          </b-input-group>
                  </div>
                </div>

                <div class="col-md-2 col-sm-4">
                <div class="bf-detail">
                <b-input-group class="mb-3">
                      <b-form-input
                        id="example-input"
                        class="form-control"
                        v-model="search.dateTo"
                        type="text"
                        style="height: 34px;"
                        placeholder="Đến ngày"
                        autocomplete="off"
                      ></b-form-input>
                      <b-input-group-append>
                        <b-form-datepicker
                          v-model="search.dateTo"
                          class="date"
                          button-only
                          right
                          locale="vi-VN"
                          style="height: 34px;"
                          aria-controls="example-input"
                        ></b-form-datepicker>
                      </b-input-group-append>
                    </b-input-group>
                </div>
                </div>

                <div class="col-md-2 col-sm-4">
                  <div class="bf-detail">
                    <Select2
                      v-model="search.position"
                      :options="optionsPosition"
                      placeholder="Vai trò"
                    >
                    </Select2>
                  </div>
                </div>
                <div class="col-md-2 col-sm-4">
                  <div class="bf-detail">
                    <Select2
                      v-model="search.status"
                      :options="optionsStatus"
                      placeholder="Trạng thái"
                    >
                    </Select2>
                  </div>
                </div>
                
                <div class="btn-fillter">
                  <div class="bf-detail" style="margin-top: 16px">
                    <button
                      @click.prevent="submitForm"
                      class="btn btn-info"
                      style="font-size: 13px; margin-right: 5px; color:white;"
                    >
                      Lọc
                    </button>
                    <button
                      @click="clearSearch"
                      class="btn btn-secondary"
                      style="font-size: 13px; margin-right: 5px;"
                    >
                      Bỏ lọc
                    </button>
                    <!-- <button
                      class="btn btn-success"
                      style="font-size: 13px; margin-right: 5px;"
                    >
                      Xuất Excel
                    </button> -->
                  </div>
                </div>
              </div>
            </div>
          </form>
        </div>

        <div class="page-content">
          <div class="card">
            <div class="card-body">
              <div class="table-responsive">
                <table class="table table-striped table-bordered mb-0">
                  <thead>
                    <tr>
                      <th style="text-align: center">STT</th>
                      <th>Họ và tên</th>
                      <th>Email</th>
                      <th>Số Điện Thoại</th>
                      <th>Vai Trò</th>
                      <th>Trạng Thái</th>
                      <th>Thao tác</th>
                    </tr>
                  </thead>

                  <tbody>
                    <tr v-for="(user, index) in dataUsers" :key="index">
                      <td style="text-align: center">
                        {{ search.size * (search.page - 1) + index + 1 }}
                      </td>
                      <td>{{ user.fullName }}</td>
                      <td>{{ user.username }}</td>
                      <td>{{ user.phoneNumber }}</td>
                      <td>
                        <span v-if="user.position === 'BOD'">
                          Bod
                        </span>
                        <span v-if="user.position === 'TECHNICIAN'">
                          Kỹ thuật
                        </span>
                        <span v-if="user.position === 'OPERATOR'">
                          Vận hành
                        </span>
                        <span v-if="user.position === 'ACCOUNTANT'">
                          Kế toán
                        </span>
                      </td>
                      <td>
                        <span
                          :class="
                            user.status == 'INACTIVE'
                              ? 'badge badge-pill badge-soft-danger'
                              : 'badge badge-pill badge-soft-success'
                          "
                        >
                          {{ user.status == "INACTIVE" ? "Khóa" : "Hoạt động" }}
                        </span>
                      </td>
                      <td>
