                            <b-icon icon="three-dots-vertical"></b-icon>
                          </template>
                          <div style="font-size: 13px;">
                            <b-dropdown-item @click="detail(user.id)">
                              Chi tiết
                            </b-dropdown-item>
                            <b-dropdown-item @click="update(user.id)">
                              Cập nhật
                            </b-dropdown-item>
                            <b-dropdown-item @click="assign(user.id)">
                              Gán Quyền
                            </b-dropdown-item>
                            <b-dropdown-item
                              @click="lock(user.id)"
                              v-if="user.status === 'ACTIVE' || user.status === 'REQUIRED_PASSWORD_CHANGE'"
                            >
                              Khóa
                            </b-dropdown-item>
                            <b-dropdown-item
                              @click="$bvModal.show(user.id)"
                              v-if="user.status === 'INACTIVE'"
                            >
