                </table>
              </div>

              <div class="overflow-auto">
                <b-pagination
                  v-model="search.page"
                  :total-rows="pagination.total"
                  :per-page="search.size"
                  first-text="<<"
                    prev-text="<"
                    next-text=">"
                    last-text=">>"
                  class="pagination mt-4"
                ></b-pagination>
              </div>

            </div>
          </div>
        </div>
      </div>

      <footer-content />
    </div>

    <!-- -----------modal permission------- -->
    <b-modal id="bv-modal-example-error-permission" hide-footer hide-header>
      <b-col class="iconLogout mb-2">
        <b-icon
          icon="x-circle"
          class="iconsBox"
          style="color: red!important;"
        ></b-icon>
      </b-col>
      <div class="d-block text-center">
        <h3
          style="font-size: 1.21875rem; color: rgb(73, 80, 87); margin-bottom: .5rem;font-weight: 500;line-height: 1.2;"
        >
          Bạn không có quyền truy cập
        </h3>
      </div>
      <div class="buttonSubmitLogout">
        <button
          class="buttonOK mt-3"
          @click="$bvModal.hide('bv-modal-example-error-permission')"
          style="font-size: 13px;"
        >
          OK
        </button>
      </div>
    </b-modal>
    <!-- -----------end modal permission------- -->
  </div>
</template>

<script>
import index from "../../components/index.vue";
import FooterContent from "../../components/FooterContent.vue";
import { UserService } from "@/services/user.service";
import prepareQueryParamsMixin from '../../mixins/prepareQueryParamsMixin'
import 'vue2-datepicker/index.css';
import Select2 from 'v-select2-component';
// import moment from 'moment'

export default {
  components: { index, FooterContent,  Select2 },
  name: "list-user",
  mixins: [prepareQueryParamsMixin],
  data() {
    return {
      token: localStorage.getItem("token"),
      idUser: this.$route.params.id,
      DATA_PERMISSION: localStorage.getItem("permission"),
      dataUsers: [],
      dateRange: {
        from: null,
        to: null
      },
      search: {
        page: 1,
        size: 10
      },
      pagination: {
        total: 20
      },
      optionsPosition: [
        { id: 'BOD', text: 'Bod' },
        { id: 'TECHNICIAN', text: 'Kỹ thuật' },
        { id: 'OPERATOR', text: 'Vận hành' },
        { id: 'ACCOUNTANT', text: 'Kế toán' }
      ],
      optionsStatus: [
        { id: 'ACTIVE', text: 'Hoạt động' },
        { id: 'INACTIVE', text: 'Khóa' }
      ]
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        if (!this.search.dateFrom) delete this.search.dateFrom
        if (!this.search.dateTo) delete this.search.dateTo
        // this.search.dateFrom = moment(this.dateRange.from).startOf('day').add('7', 'hours')
        // this.search.dateTo = moment(this.dateRange.to).endOf('day').add('7', 'hours')

        const response = await UserService.getList(this.token, this.search);
        if (response.status === 200) {
          this.dataUsers = response.data.data;
          this.pagination.total = response.data.total
        }
      } catch (error) {
        return error
      }
    },

    detail(userId) {
      if (this.DATA_PERMISSION.includes("VIEW_USER_DETAIL")) {
        this.$router.push({ name: "Chi Tiết User", params: { id: userId } });
      } else this.$bvModal.show("bv-modal-example-error-permission");
    },

    update(userId) {
      if (this.DATA_PERMISSION.includes("UPDATE_USER")) {
        this.$router.push({ name: "Cập nhật User", params: { id: userId } });
      } else this.$bvModal.show("bv-modal-example-error-permission");
    },

    assign(userId) {
      if (this.DATA_PERMISSION.includes("ASSIGN_USER_PERMISSION")) {
        this.$router.push({ name: "Gán nhóm quyền", params: { id: userId } });
      } else this.$bvModal.show("bv-modal-example-error-permission");
    },

    async lock(userId) {
      if (this.DATA_PERMISSION.includes("LOCK_USER")) {
        const response = await UserService.lockUser(userId, this.token);
        if (response.status === 200) {
          this.fetchData()
        }
      } else this.$bvModal.show("bv-modal-example-error-permission");
    },

    async unlock(userId) {
      if (this.DATA_PERMISSION.includes("LOCK_USER")) {
        const response = await UserService.unlockUser(userId, this.token);
        if (response.status === 200) {
          this.fetchData()
        }
      }
    },

    submitForm () {
      this.pagination.page = 1
      this.fetchData()
    },

    clearSearch () {
      this.dateRange.from = null
      this.dateRange.to = null
      this.search = {
        page: 1,
        size: 10
      }
      this.fetchData()
    }
  },

  computed: {
    useInUrlQueryPropList () {
      return this.prepareQueryParamsMixin({
        page: this.search.page
      })
    }
  },

  watch: {
    'search.page': function () {
      this.$router.push({
        path: '/list-user',
        query: this.useInUrlQueryPropList
      })
      this.fetchData()
    }
  },
};
</script>

