<template>
  <div class="homePages">
    <index />
    <div class="content-page">
      <div class="menu-content">
        <div class="main-content">
          <div class="page-content">
            <!-- start page title -->
            <div class="row">
              <div class="col-12">
                <div
                  class="page-title-box d-flex align-items-center justify-content-between"
                >
                  <h4 class="mb-0 font-size-18">Quản lý giao dịch</h4>
                </div>
              </div>
            </div>
            <!-- end page title -->

            <div class="row">
              <div class="col-xl-12">
                <div class="row">
                  <div @click="fetchDataNew" class="col-md-4">
                    <div class="card mini-stats-wid">
                      <div class="card-body" id="text-media">
                        <div class="media">
                          <div class="titleNew media-body">
                            <p class="titleNew font-weight-medium">
                              Tổng GD chưa xử lý
                            </p>
                            <h4 class="mb-0 new">{{ total.new }}</h4>
                          </div>
                          <div class="bg-primary" style=" border-radius: 50%;">
                            <v-avatar>
                              <v-icon large class="icon-media">
                                mdi-folder
                              </v-icon>
                            </v-avatar>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div @click="fetchDataProcessing" class="col-md-4">
                    <div class="card mini-stats-wid">
                      <div class="card-body" id="text-media">
                        <div class="media">
                          <div class="titleProcessing media-body">
                            <p class="titleProcessing font-weight-medium">
                              Tổng GD đang xử lý
                            </p>
                            <h4 class="mb-0 processing">
                              {{ total.process }}
                            </h4>
                          </div>
                          <div class="bg-primary" style=" border-radius: 50%;">
                            <v-avatar>
                              <v-icon large class="icon-media">
                                mdi-file
                              </v-icon>
                            </v-avatar>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div @click="fetchDataDone" class="col-md-4">
                    <div class="card mini-stats-wid">
                      <div class="card-body" id="text-media">
                        <div class="media">
                          <div class="titleDone media-body">
                            <p class="titleDone  font-weight-medium">
                              Tổng GD đã xử lý
                            </p>
                            <h4 class="mb-0 done">{{ total.done }}</h4>
                          </div>
                          <div class="bg-primary" style=" border-radius: 50%;">
                            <v-avatar>
                              <v-icon large class="icon-media">
                                mdi-arrow-up-bold-box-outline
                              </v-icon>
                            </v-avatar>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <!-- end row -->
            </div>
          </div>
          <!-- end row -->

          <div class="row">
            <div class="col-lg-12">
              <div class="card">
                <div class="card-body" style="padding: 8px">
                  <div class="table-responsive">
                    <table class="table table-striped table-bordered mb-0">
                      <thead class="thead-light">
                        <tr style="font-size: 12px;white-space: nowrap;">
                          <th>STT</th>
                          <th>Thời gian</th>
                          <th>App</th>
                          <th>Giao dịch</th>
                          <th>Mã Giao Dịch</th>
                          <th>Khách Hàng</th>
                          <th>Số tiền</th>
                          <th>Loại Rủi Ro</th>
                          <th>Trạng thái</th>
                          <th>Người xử lý</th>
                          <th>Thao tác</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr
                          style="font-size: 12px"
                          v-for="(transaction, index) in dataTransactions"
                          :key="index"
                        >
                          <td style="text-align: center">
                            {{ params.size * (params.page - 1) + index + 1 }}
                          </td>
                          <td>
                            <span>
                              {{ transaction.transactionTime | formatDateTime }}
                            </span>
                          </td>
                          <td>Vimo</td>
                          <td>{{ transaction.methodName }}</td>
                          <router-link :to="`/list-risk/detail?code=${transaction.transactionCode}`">
                          <td >{{ transaction.transactionCode }}</td>
                          </router-link>
                          <td>
                            <span>Tên: {{ transaction.customerName }}</span>
                            <span style="display: block">
                              SĐT: {{ transaction.customerIdf }}
                            </span>
                          </td>
                          <td>
                            {{ transaction.amount | formatCurrency }}
                          </td>
                          <td v-if="transaction.rulesDescription" class="rules">
                            <span
                            class="rule"
                              v-for="(rule,
                              index) in transaction.rulesDescription"
                              :key="index"
                            >
                              {{ rule }}
                            </span>
                          </td>
                          <td>
                            <span
                              v-if="transaction.statusAction === 'NEW'"
                              class="badge badge-pill badge-soft-primary"
                            >
                              Chờ xử lý
                            </span>
                            <span
                              v-if="transaction.statusAction === 'DONE'"
                              class="badge badge-pill badge-soft-success"
                            >
                              Đã xử lý
                            </span>
                            <span
                              v-if="transaction.statusAction === 'PROCESSING'"
                              class="badge badge-pill badge-soft-danger"
                            >
                              Đang xử lý
                            </span>
                          </td>
                          <td>{{ transaction.nrmUsername }}</td>
                          <td>
                            <b-dropdown
                              right
                              text=""
                              variant="none"
                              class="three-dot"
                              no-caret
                            >
                              <template #button-content>
                                <b-icon icon="three-dots-vertical"></b-icon>
                              </template>
                              <div style="font-size: 13px;">
                                <router-link
                                  v-if="
                                    DATA_PERMISSION.includes('VIEW_DETAIL_TRAN')
                                  "
                                  class="dropdown-item"
                                  :to="
                                    `/list-risk/detail?code=${transaction.transactionCode}`
                                  "
                                >
                                  Chi tiết
                                </router-link>
                                <b-dropdown-item
                                  v-if="transaction.statusAction === 'NEW'"
                                  @click="
                                    setProcessing(transaction.transactionCode)
                                  "
                                >
                                  Nhận giao dịch
                                </b-dropdown-item>
                                <b-dropdown-item
                                  v-if="
                                    transaction.statusAction === 'PROCESSING' &&
                                      transaction.nrmUsername === email
                                  "
                                  @click="setDone(transaction.transactionCode)"
                                >
                                  Hoàn tất
                                </b-dropdown-item>
                                <b-dropdown-item
                                  v-if="
                                    transaction.statusAction === 'PROCESSING' &&
                                      transaction.nrmUsername === email
                                  "
                                  @click="
                                    changeNew(transaction.transactionCode)
                                  "
                                >
                                  Chuyển đổi
                                </b-dropdown-item>
                                <!-- --------- modal xác nhận chuyển đổi -------->
                                <b-modal
                                  :id="transaction.transactionCode"
                                  hide-footer
                                  hide-header
                                >
                                  <b-col class="iconLogout mb-2"> </b-col>
                                  <div class="d-block text-center">
                                    <h3
                                      style="font-size: 1.21875rem; color: rgb(73, 80, 87); margin-bottom: .5rem;font-weight: 500;line-height: 1.2;"
                                    >
                                      Nhập lý do chuyển đổi
                                    </h3>
                                    <div>
                                      <b-card>
                                        <b-input v-model="reasonTransfer" />
                                      </b-card>
                                    </div>
                                  </div>
                                  <div class="buttonSubmitLogout">
                                    <v-btn
                                      class="buttonOK mt-3 "
                                      @click="
                                        setNew(transaction.transactionCode)
                                      "
                                      style="font-size: 13px;color:#ffff"
                                      :disabled="buttonDisable"
                                    >
                                      OK
                                    </v-btn>
                                    <v-btn
                                      class="buttonOK mt-3"
                                      color="success"
                                      @click="
                                        $bvModal.hide(
                                          transaction.transactionCode
                                        )
                                      "
                                      style="font-size: 13px;"
                                    >
                                      Hủy
                                    </v-btn>
                                  </div>
                                </b-modal>
                                <!-- --------- end modal xác nhận chuyển đổi -------->
                              </div>
                            </b-dropdown>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>

                  <div class="overflow-auto">
                    <b-pagination
                      v-model="params.page"
                      :total-rows="pagination.total"
                      :per-page="params.size"
                      first-text="<<"
                      prev-text="<"
                      next-text=">"
                      last-text=">>"
                      class="pagination mt-4"
                    ></b-pagination>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- End Page-content -->

        <!-- All Modal -->
        <!-- Modal permission-->
        <b-modal id="bv-modal-example-error-permission" hide-footer hide-header>
          <b-col class="iconLogout mb-2">
            <b-icon
              icon="x-circle"
              class="iconsBox"
              style="color: red!important;"
            ></b-icon>
          </b-col>
          <div class="d-block text-center">
            <h3
              style="font-size: 1.21875rem; color: rgb(73, 80, 87); margin-bottom: .5rem;font-weight: 500;line-height: 1.2;"
            >
              Bạn không có quyền truy cập
            </h3>
          </div>
          <div class="buttonSubmitLogout">
            <button
              class="buttonOK mt-3"
              @click="$bvModal.hide('bv-modal-example-error-permission')"
              style="font-size: 13px;"
            >
              OK
            </button>
          </div>
        </b-modal>
        <!-- End modal permission-->

        <!-- Modal nhận giao dịch thành công -->
        <b-modal id="bv-modal-set-processing-done" hide-footer hide-header>
          <b-col class="iconLogout mb-2">
            <div class="mb-img mb-4">
              <span><img src="./../assets/images/sussecc.svg" alt=""/></span>
            </div>
          </b-col>
          <div class="d-block text-center">
            <h3
              style="font-size: 1.21875rem; color: rgb(73, 80, 87); margin-bottom: .5rem;font-weight: 500;line-height: 1.2;"
            >
              Nhận xử lý giao dịch thành công
            </h3>
          </div>
          <div class="buttonSubmitLogout">
            <button
              class="buttonOK mt-3"
              style="font-size: 13px;"
              @click="$bvModal.hide('bv-modal-set-processing-done')"
            >
              OK
            </button>
          </div>
        </b-modal>
        <!-- End modal nhận giao dịch thành công -->

        <!-- Modal hoàn tất giao dịch thành công -->
        <b-modal id="bv-modal-set-processed-done" hide-footer hide-header>
          <b-col class="iconLogout mb-2">
            <div class="mb-img mb-4">
              <span><img src="./../assets/images/sussecc.svg" alt=""/></span>
            </div>
          </b-col>
          <div class="d-block text-center">
            <h3
              style="font-size: 1.21875rem; color: rgb(73, 80, 87); margin-bottom: .5rem;font-weight: 500;line-height: 1.2;"
            >
              Hoàn tất giao dịch thành công
            </h3>
          </div>
          <div class="buttonSubmitLogout">
            <button
              class="buttonOK mt-3"
              style="font-size: 13px;"
              @click="$bvModal.hide('bv-modal-set-processed-done')"
            >
              OK
            </button>
          </div>
        </b-modal>
        <!-- End modal hoàn tất giao dịch thành công -->

        <!-- Modal chuyển đổi giao dịch thành công -->
        <b-modal id="bv-modal-set-new-done" hide-footer hide-header>
          <b-col class="iconLogout mb-2">
            <div class="mb-img mb-4">
              <span><img src="./../assets/images/sussecc.svg" alt=""/></span>
            </div>
          </b-col>
          <div class="d-block text-center">
            <h3
              style="font-size: 1.21875rem; color: rgb(73, 80, 87); margin-bottom: .5rem;font-weight: 500;line-height: 1.2;"
            >
              Chuyển đổi thành công
            </h3>
          </div>
          <div class="buttonSubmitLogout">
            <button
              class="buttonOK mt-3"
              style="font-size: 13px;"
              @click="$bvModal.hide('bv-modal-set-new-done')"
            >
              OK
            </button>
          </div>
        </b-modal>
        <!-- End modal chuyển đổi giao dịch thành công -->

        <!-- end modal -->
      </div>
      <footer-content />
    </div>
  </div>
</template>

<script>
import Index from "../components/index.vue";
import FooterContent from "../components/FooterContent.vue";
import { TransactionService } from "@/services/transaction.service";
import prepareQueryParamsMixin from '../mixins/prepareQueryParamsMixin'
// import { required, } from "vuelidate/lib/validators";

export default {
  name: "home-page",
  mixins: [prepareQueryParamsMixin],
  components: {
    Index,
    FooterContent,
  },
  data() {
    return {
      dataTransactions: [],
      params: {
        page: 1,
        size: 10,
      },
      pagination: {
        total: 0,
      },
      search: {},
      email: localStorage.getItem("email"),
      DATA_PERMISSION: localStorage.getItem("permission"),
      reasonTransfer: '',
      total:{
        new:"",
        process:"",
        done:""
      },
      description:"",
      emailHome :localStorage.getItem("email")
    };
  },
  // validations: {
  //               reasonTransfer: { required },
  //       },
    
  created() {
    this.search.statusAction = "NEW";
    this.fetchData();
    this.getListHome();
  },

  methods: {
    fetchDataNew() {
      delete this.search.nrmUsername;
      this.search.statusAction = "NEW";
      this.params.page = 1;
      this.fetchData();
      this.getListHome();
    },

    fetchDataProcessing() {
      this.search.nrmUsername = localStorage.getItem("email");
      this.search.statusAction = "PROCESSING";
      this.params.page = 1;
      this.fetchData();
      this.getListHome();
    },

    fetchDataDone() {
      this.search.nrmUsername = localStorage.getItem("email");
      this.search.statusAction = "DONE";
      this.params.page = 1;
      this.fetchData();
      this.getListHome();
    },

    async fetchData() {
      try {
        const response = await TransactionService.getList(
          this.params,
          this.search
        );
        if (response.status === 200) {
          this.dataTransactions = response.data.riskTransList;
          this.pagination.total = response.data.total;
        }
      } catch (error) {
        return error
      }
    },

    async setProcessing(transactionCode) {
      if (this.DATA_PERMISSION.includes("PROCESSING_RISK")) {
        try {
          const response = await TransactionService.setProcessing(
            this.email,
            transactionCode
          );
          if (response.status === 200) {
            this.$bvModal.show("bv-modal-set-processing-done");
            this.fetchData();
            this.getListHome();
          }else {
            alert(response.error)
          }
        } catch (error) {
          return error
        }
      } else {
        this.$bvModal.show("bv-modal-example-error-permission");
      }
    },

    async setDone(transactionCode) {
      if (this.DATA_PERMISSION.includes("DONE_RISK")) {
        try {
          const response = await TransactionService.setProcessed(
            this.email,
            transactionCode
          );
          if (response.status === 200) {
            this.$bvModal.show("bv-modal-set-processed-done");
            this.fetchData();
            this.getListHome();
          }
        } catch (error) {
          return error
        }
      } else {
        this.$bvModal.show("bv-modal-example-error-permission");
      }
    },

    changeNew(transactionCode) {
      if (this.DATA_PERMISSION.includes("PROCESSING_RISK")) {
        this.$bvModal.show(transactionCode);
      } else {
        this.$bvModal.show("bv-modal-example-error-permission");
      }
    },

    async setNew(transactionCode) {
      try {
        const body = {
          nrmUsername: this.email,
          reasonTransfer: this.reasonTransfer,
        };
        const response = await TransactionService.transfer(
          transactionCode,
          body
        );
        if (response.status === 200) {
          this.$bvModal.hide(transactionCode);
          this.$bvModal.show("bv-modal-set-new-done");
          this.fetchData();
          this.getListHome();
        }
        else{
          alert(response.message)
        }
      } catch (error) {
        return error
      }
    },
    async getListHome() {
      try {
        const response = await TransactionService.getListHome(this.emailHome);
        if (response.status == 200) {
          this.total.new = response.data[0].New.total;
          this.total.process = response.data[0].Processing.total;
          this.total.done = response.data[0].Done.total;
        }
      } catch (error) {
        return error;
      }
    },
  },

  computed: {
    useInUrlQueryPropList () {
      return this.prepareQueryParamsMixin({
        page: this.params.page
      })
    },
    //   descriptionError(){
    //     const errors = []
    //     if (!this.$v.reasonTransfer.$dirty) return errors;
    // return this.reasonTransfer == ""
    //     ? "Lý do chuyển đ buộc nhập"
    //     : this.reasonTransfer.length <= 5
    //     ? ""
    //     : "E-mail sai định dạng";
    //   }
    buttonDisable(){
            return this.reasonTransfer.length <=5 
        }
  },

  watch: {
    "params.page": function() {
      this.$router.push({
        path: '/home-page',
        query: this.useInUrlQueryPropList
      })
      this.fetchData();
      this.getListHome();
    },
  },
};
</script>

<style scoped>
