  background-color: #ffff;
  padding-left: 230px;
}
.main-content {
  background-color: #f8f8fb !important;
  /* padding: 20px; */
  margin-top: 70px !important;
}
.nav {
  background-color: #fff;
}
.pagination {
  justify-content: flex-end !important;
}
.tabs {
  margin: 0px !important;
  background-color: #fff;
}
