  color: #008bf4 !important;
}
.titleProcessing {
  color: #fcb63e !important;
}
.titleDone {
  color: #3bb54a !important;
}
.form-find {
  display: flex;
}
.btn-fillter {
  margin-top: 12px;
}

#example-input {
  font-size: 13px;
}
.new {
  font-size: 20px;
}
.processing {
  font-size: 20px;
}
.done {
  font-size: 20px;
}
.router {
  color: black !important;
}
.rules {
  display: block;
  color: #cf232a;
}
.rule{
  display: block; 
  padding-bottom:5px;
}
